nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  width: 100%;
  height: 70px;
  padding: 00px 350px 0px 330px;
  box-shadow: 0 2px 3px #729cff;
}

.logo {
  font-size: 18px;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  align-items: center;
  justify-content: center;
  display: flex;
  color: white;
  transition: 0.2s;
}

#logo-link {
  text-decoration: none;
}

#navbar-heads {
  padding-top: 10px;
  display: flex;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
}

#navbar-heads li {
  list-style: none;
  padding: 0 35px;
  position: relative;
}

#navbar-heads li a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: white;
}
#navbar-heads li a:hover {
  color: #89acff;
}

#mobile {
  display: none;
}

#mobile i {
  color: white;
}

@media only screen and (max-width: 769px) {
  #navbar-heads {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 58px;
    right: -11000px;
    width: 100%;
    height: 160px;
    background-color: rgb(23, 25, 60);
    padding: 20px 0 0 10px;
    transition: 0.3s ease-in-out;
  }
  nav {
    padding: 0px 20px 0px 40px;
    width: 100%;
  }
  #logo-link {
    width: 100%;
    justify-content: center;
    display: flex;
    align-items: center;
    text-align: center;
    margin: 0 auto;
  }
  .logo {
    font-size: 16px;
  }
  #navbar-heads.active {
    right: 0px;
  }

  #navbar-heads li {
    margin-bottom: 25px;
  }
  #mobile {
    display: block;
  }
  #mobile i {
    font-size: 24px;
  }
}
