.left-main {
  width: 100%;
  height: 650px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
}

.name {
  font-size: 70px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  text-align: center;
  width: 100%;
  padding: 0px 0px 20px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.head-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.work-description {
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  line-height: 1.25;
  height: 100px;
  width: 400px;
  padding: 0px 17px 0px 17px;
}

span {
  color: black;
  font-size: 20px;
  font-weight: 600;
}

.btn {
  display: flex;
  justify-content: left;
  margin-left: 40px;
  align-items: center;
  width: 100%;
  height: fit-content;
}

.button1 {
  background-image: linear-gradient(
    to right,
    #4776e6 0%,
    #8e54e9 51%,
    #4776e6 100%
  );
  padding: 15px 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  font-weight: 600;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  font-family: "Montserrat", sans-serif;
  border-radius: 10px;
  cursor: pointer;
}

.button1:hover {
  background-position: right center;
  color: #fff;
  transform: scale(1.02);
  box-shadow: 2px 2px 0px 1px black;
}
.button1:hover img {
  transform: rotate(360deg);
  transition: 0.2s;
}

@media only screen and (max-width: 769px) {
  .left-main {
    margin-top: 120px;
    align-items: unset;
    height: 500px;
  }
  .name {
    font-size: 50px;
  }

  .work-description {
    font-size: 17px;
    width: 320px;
    padding: 20px 30px 0px 30px;
  }

  span {
    font-size: 17px;
  }

  .btn {
    justify-content: center;
    margin-left: 0px;
    margin-top: 60px;
    width: 100%;
  }
}
