.contact {
  height: fit-content;
  background-color: whitesmoke;
}

.contact-head {
  font-size: 70px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  display: flex;
  height: 180px;
  justify-content: center;
  align-items: center;
}

.contact-desc {
  font-size: 18px;
  display: flex;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 700px;
  margin: 0 auto;
}
.contact-email {
  margin-top: 10px;
  margin-bottom: 50px;
  height: 100px;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
}
.contact-email a {
  color: black;
}
.contact-email a:hover {
  color: rgb(121, 121, 121);
  cursor: pointer;
}

.socials {
  height: 95px;
  background-color: #ffffff;
}

.contact-icons {
  list-style: none;
  display: flex;
  font-size: 3rem;
  align-items: center;
  padding-top: 15px;
  justify-content: space-evenly;
}
.contact-icons a {
  font-size: 1.9rem;
  color: black;
}
.contact-icons a:hover {
  color: #215be2;
  font-size: 2.5rem;
  transition: 0.3s;
}

@media only screen and (max-width: 769px) {
  .contact-head {
    font-size: 30px;
    height: 150px;
  }

  .contact-desc {
    font-size: 17px;
    width: 100%;
    padding: 0px 20px 0px 20px;
  }

  .contact-icons {
    padding-left: 0px;
  }
  .contact-icons a {
    font-size: 1.5rem;
    color: rgb(64, 64, 64);
  }
}
