* {
  margin: 0;
  box-sizing: border-box;
}
.works-head {
  font-size: 70px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.works-section {
  padding: 50px;
  place-items: center;
  display: grid;
  background-color: rgb(96, 96, 248, 0.06);
}

.works-row {
  padding-top: 3rem;
  display: grid;
  grid-template-columns: 430px 430px;
}

/*1//////////////////*/

.works-col1 {
  justify-content: center;
  display: flex;
  padding-bottom: 50px;
  align-items: center;
}
.works-cover1 {
  height: 248px;
  width: 369px;
}
.works-card1 {
  box-shadow: 12px 12px 0px 1px #315066;
  transition: 0.2s;
  cursor: pointer;
}
.works-cover1 img {
  width: 369px;
  transition: 0.3s;
}
.works-desc1 {
  width: 369px;
  height: 220px;
  background-color: #3e6488;
}
.works-desc1 .projName {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: white;
  padding: 20px;
}
.works-desc1,
p {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  color: white;
  padding: 10px 25px 10px 25px;
}
.works-card1:hover {
  transform: scale(1.04);
  border-radius: 5px;
}

